<template>
  <v-row>
    <v-col cols="6">
      <v-textarea readonly v-model="state" />
      <v-checkbox dense v-model="prettyExport" label="pretty"></v-checkbox>
      <br />
      <v-btn @click="doExport">Download</v-btn>
    </v-col>
    <v-col cols="6">
      <v-textarea v-model="newState"></v-textarea>
      <v-checkbox dense v-model="prettyImport" label="pretty"></v-checkbox>
      <br />
      <v-btn :disabled="!valid" @click="doImport">Import</v-btn>
    </v-col>
  </v-row>
</template>
<script>
export default {
  name: "ImportExport",
  data: () => ({
    newState: "",
    prettyExport: false,
    prettyImport: false
  }),
  computed: {
    state: function() {
      if (this.prettyExport) return JSON.stringify(this.$store.state, null, 2);
      else return JSON.stringify(this.$store.state);
    },
    valid: function() {
      if (this.newState == "") return false;
      try {
        JSON.stringify(JSON.parse(this.newState));
        return true;
      } catch (e) {
        return false;
      }
    }
  },
  watch: {
    newState: function(newVal) {
      try {
        if (this.prettyImport)
          this.newState = JSON.stringify(JSON.parse(newVal), null, 2);
      } catch (e) {
        console.log(e);
      }
    },
    pretty: function(newVal) {
      try {
        if (newVal) {
          this.newState = JSON.stringify(JSON.parse(newVal), null, 2);
        } else {
          this.newState = JSON.stringify(JSON.parse(newVal));
        }
      } catch (e) {
        console.log(e);
      }
    }
  },
  methods: {
    doImport: function() {
      this.$store.replaceState(JSON.parse(this.newState));
    },
    doExport: function() {
      var element = document.createElement("a");
      element.setAttribute(
        "href",
        "data:text/plain;charset=utf-8," + encodeURIComponent(this.state)
      );
      element.setAttribute("download", "tarkov-export.json");
      element.style.display = "none";
      document.body.appendChild(element);
      element.click();
      document.body.removeChild(element);
    }
  }
};
</script>
